import {ChangeDetectorRef, Component, Input, OnDestroy, OnInit} from '@angular/core';
import {VideoContext} from "../../../../Classes/Classes";
import {AppContextService} from "../../../../services/app-context.service";
import {WebRtcComponent} from "../web-rtc.component";
import {WebRtcService} from "../../../../services/web-rtc.service";

@Component({
  selector: 'app-video-channel',
  templateUrl: './video-channel.component.html',
  styleUrls: ['./video-channel.component.css']
})
export class VideoChannelComponent implements OnInit, OnDestroy {
    
    public localBadge = '';
    public subscriptions = [];
    public bottomVideo = 'video';
    public bottomAudio = 'audio';
    public topPeerVideoImg = 'video';
    public topPeerAudioImg = 'audio';
    public buttons = [['video-off', 'video'], ['audio-off', 'audio']];
    
    @Input() public videoContext : VideoContext;
  constructor(
      public appContext : AppContextService,
      public webRtcService : WebRtcService,
      public changeRef : ChangeDetectorRef) {
  }

  ngOnInit() {
      
      this.videoContext.local && this.subscriptions.push(this.appContext.announcements.subscribe(announcements => {
          let length = announcements.filter(ann => ann.active).length;
	  this.localBadge = length == 0 ? '' : length + '' ;
      })) ;
      
      this.subscriptions.push(this.videoContext.dataChannel.data.subscribe(message => {
            //Прилетели данные по каналу данных
	  //message : {type : settings, video : number, audio : number}
	  //  {type : status, status : StatusColorsService}
	  //Получено удаленное сообщение о данных удаленного пользователя
	  let data = JSON.parse(message.data);
	  if(data.type === "settings"){
	      'video' in data && (this.topPeerVideoImg = this.buttons[0][data.video]);
	      'audio' in data && (this.topPeerAudioImg = this.buttons[1][data.audio]);
	  }else if(data.type === "status"){
	      let contact = this.videoContext.contact.value;
	      contact.statusColor = data.statusColor;
	      this.videoContext.contact.next(contact);
	      //если статус - один из статусов невозможности продолжения соединения
	      this.webRtcService.checkConnectionState({state : data.statusText, contact : contact, videoContext : this.videoContext});
	  }
	  this.changeRef.detectChanges();
      }));
  }
   ngOnDestroy(): void {
       this.subscriptions.forEach(sub => sub.unsubscribe());
   }
    
    clickIcon(inx){
      let message;
        switch (inx) { //Изменение видеорежима
	    case 0 :
		let videoTrack = this.videoContext.stream.value.getVideoTracks()[0];
		videoTrack && (videoTrack.enabled = !(this.bottomVideo == 'video'));
		message = {type : 'settings', video :  Number(videoTrack.enabled) } ;
		this.bottomVideo =  this.buttons[0][+ videoTrack.enabled] ;
		break;    //Изменение аудиорежима
	    case 1 :
		let audioTrack = this.videoContext.stream.value.getAudioTracks()[0];
		audioTrack && (audioTrack.enabled = !(this.bottomAudio === 'audio'));
		message = {type : 'settings', audio : Number(audioTrack.enabled)} ;
		this.bottomAudio =  this.buttons[1][+ audioTrack.enabled];
		break;
	    case 2 :
		 //Остановка видеозвонка
	        this.webRtcService.stopVideoChannel(this.videoContext);
	}
	//Отправить данные об изменении значений настроек всем удаленным каналам
	this.sendDataMessages(message) ;
    }
    
    sendDataMessages(message){
	let webRtcContext = this.appContext.webRtcContexts.getContext(this.videoContext.webRtcUid),
	    local = this.videoContext.contact.value.uid === this.appContext.appUser.uid;
	webRtcContext && webRtcContext.videoCollection.forEach(videoContext =>{
	    if((local && (videoContext.contact.value.uid !== this.videoContext.contact.value.uid)) || (!local && (videoContext.contact.value.uid === this.videoContext.contact.value.uid))){
		if(!!videoContext.dataChannel.channel && videoContext.dataChannel.channel.readyState === 'open'){
		    videoContext.dataChannel.channel.send(JSON.stringify(message));
		}
	    }
	})
    }
    
    onLoadedData(){
	this.videoContext.className.active = true;
	this.webRtcService.checkVideoContexts(this.videoContext);
    }
    onEnded(){
	this.videoContext.className.active = false;
	this.webRtcService.checkVideoContexts(this.videoContext);
    }
}
